@font-face {
  font-family: "Inter";
  src: url("./Inter-Regular.eot");
  src: url("./Inter-Regular.woff") format("woff"),
    url("./Inter-Regular.ttf") format("truetype"),
    url("./Inter-Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  src: url("./Inter-Black.eot");
  src: local("☺"), url("./Inter-Black.woff") format("woff"),
    url("./Inter-Black.ttf") format("truetype"),
    url("./Inter-Black.svg") format("svg");
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  src: url("./Inter-Bold.eot");
  src: local("☺"), url("./Inter-Bold.woff") format("woff"),
    url("./Inter-Bold.ttf") format("truetype"),
    url("./Inter-Bold.svg") format("svg");
  font-weight: bold;
  font-style: normal;
}
